<template>
  <div class="div_list_content" @dblclick="playAudio">
    <img :src="img_src"  alt="">
    <div class="div_text">
      <p class="p_title">{{ title }}</p><br>
      <p class="p_artist">{{ artist }}</p>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ListContent",
  props: {
    title: String,
    artist: String,
    id: String,
    albumId: String,
    pos: Number,
    url: String
  },
  data() {
    return {
      img_src: String,
      audioId: this.id,
      hostUrl: this.url,
      index: this.pos,
    }
  },
  created() {
    this.img_src = this.url + 'getAlbumArt?albumId=' + this.albumId
  },
  methods: {
    playAudio() {
      axios(this.hostUrl + 'playAudio?id=' + this.id + '&pos=' + this.index)
    }
  }
}
</script>

<style scoped>

.div_list_content {
  height: 60px;
  float: left;
  width: 100%;
}

.div_list_content img {
  float: left;
  height: 40px;
  width: 40px;
  margin: 10px;
}

.div_list_content:hover {
  background-color: #ddd;
  color: black;
}

.div_list_content * {
  cursor:default;
}

.div_text {
  float: left;
  vertical-align: middle;
  margin-top: 10px;
}

.div_text p {
  margin: 0;
}

.p_title {
  float: left;
  size: 2px;
}

.p_artist {
  float: left;
  size: 1px;
  color: #666;
}

</style>