<template>
  <div class="content_list_div" id="content_list_div"></div>
</template>

<script>
import Vue from "vue";
import ListContent from "@/components/ListContent";
import axios from "axios";

const ListContentClass = Vue.extend(ListContent)

export default {
  name: "ContentList",
}

function getAppHostUrl(protocol, hostAddress, hostPort) {
  if (hostAddress.includes('localhost') || hostAddress.includes('127.0.0.1')) {
    return protocol + '//192.168.1.100:1552/'
  }
  if (hostAddress.includes(':' + hostPort)) {
    return protocol + '//' + hostAddress + '/'
  }
  return protocol + '//' + hostAddress + ':' + hostPort + '/';
}

let hostUrl = getAppHostUrl(window.location.protocol, window.location.host, window.location.port);

axios(hostUrl + 'getMusicList').then((response) => {
  // updateMusicList(response.data['list'])
  const contentListDiv = document.getElementById('content_list_div')
  response.data['list'].forEach((jsonObject, index) => {
    let instance = new ListContentClass({
      propsData: {
        title: jsonObject['title'],
        artist: jsonObject['artist'],
        id: jsonObject['id'],
        albumId: jsonObject['albumId'],
        pos: index,
        url: hostUrl
      }
    })
    instance.$mount()
    contentListDiv.appendChild(instance.$el)
  })
})

</script>

<style>

.content_list_div {
  margin-top: 46px;
  margin-left: 15%;
  margin-right: 15%;
}

</style>